
import Vue from "vue";
import store from "@/store";
import moment from "moment";

import Chart from "chart.js/auto";

export default Vue.extend({
  name: "work-positions",

  data: () => ({
    datacollection: null
  }),

  async beforeRouteEnter(to, from, next) {
    try {
      next(vm => {});
    } catch (e) {
      await store.dispatch("alert/showError", e.message);
    }
  },

  mounted() {
    this.chartConfig = {
      "pie-group-services": {
        type: "pie",
        responsive: true,
        data: {
          labels: ["ATA", "Certificate", "Expertiza", "Evaluare", "AAIJ"],
          datasets: [
            {
              label: "# of Votes",
              data: [12, 19, 3, 5, 2],
              backgroundColor: ["orange", "blue", "violet", "green", "gray"],
              borderColor: [
                "rgba(255, 99, 132, 1)",
                "rgba(54, 162, 235, 1)",
                "rgba(255, 206, 86, 1)",
                "rgba(75, 192, 192, 1)",
                "rgba(153, 102, 255, 1)"
              ],
              borderWidth: 1
            }
          ]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true
            }
          }
        }
      },
      "bar-group-statuses": {
        type: "bar",
        data: {
          labels: ["ATA", "Certificate", "Expertiza", "Evaluare", "AAIJ"],
          datasets: [
            {
              label: "Așteptare",
              data: [100, 50, 10, 15, 20],
              borderColor: "orange",
              backgroundColor: "orange"
            },
            {
              label: "Confirmate",
              data: [30, 10, 10, 5, 11],
              borderColor: "red",
              backgroundColor: "blue"
            },
            {
              label: "Achitate",
              data: [50, 20, 10, 15, 20],
              borderColor: "violet",
              backgroundColor: "violet"
            },
            {
              label: "Finisate",
              data: [100, 50, 10, 15, 20],
              borderColor: "green",
              backgroundColor: "green"
            }
          ]
        },
        options: {
          responsive: true,
          plugins: {
            legend: {
              position: "top"
            }
          }
        }
      },
      "bar-online-vs-offline": {
        type: "bar",
        data: {
          labels: ["ATA", "Certificate", "Expertiza", "Evaluare", "AAIJ"],
          datasets: [
            {
              label: "Online",
              data: [100, 50, 10, 15, 20],
              borderColor: "orange",
              backgroundColor: "orange"
            },
            {
              label: "Offline",
              data: [30, 10, 10, 5, 11],
              borderColor: "red",
              backgroundColor: "blue"
            }
          ]
        },
        options: {
          responsive: true,
          plugins: {
            legend: {
              position: "top"
            }
          }
        }
      },
      "bar-group-months": {
        type: "bar",
        data: {
          labels: [
            "Sep 20",
            "Oct 20",
            "Noi 20",
            "Dec 20",
            "Ian 21",
            "Feb 21",
            "Mar 21",
            "Iun 21",
            "Iul 21",
            "Aug 21"
          ],
          datasets: [
            {
              label: "ATA",
              data: this.fillLastYear(),
              borderColor: "orange",
              backgroundColor: "orange"
            },
            {
              label: "Certificate",
              data: this.fillLastYear(),
              borderColor: "red",
              backgroundColor: "blue"
            },
            {
              label: "Expertiza",
              data: this.fillLastYear(),
              borderColor: "violet",
              backgroundColor: "violet"
            },
            {
              label: "Evaluare",
              data: this.fillLastYear(),
              borderColor: "green",
              backgroundColor: "green"
            },
            {
              label: "AAIJ",
              data: this.fillLastYear(),
              borderColor: "gray",
              backgroundColor: "gray"
            }
          ]
        },
        options: {
          responsive: true,
          plugins: {
            legend: {
              position: "top"
            }
          }
        }
      },
      "created-last-month": {
        type: "line",
        data: {
          labels: this.getDaysBetweenDates(
            moment().subtract(30, "days"),
            moment()
          ),
          datasets: [
            {
              label: "Așteptare",
              data: this.generateLast30DaysData(),
              borderColor: "orange",
              backgroundColor: "orange"
            },
            {
              label: "Confirmate",
              data: this.generateLast30DaysData(),
              borderColor: "red",
              backgroundColor: "blue"
            },
            {
              label: "Achitate",
              data: this.generateLast30DaysData(),
              borderColor: "violet",
              backgroundColor: "violet"
            },
            {
              label: "Finisate",
              data: this.generateLast30DaysData(),
              borderColor: "green",
              backgroundColor: "green"
            }
          ]
        },
        options: {
          responsive: true,
          plugins: {
            legend: {
              position: "top"
            },
            title: {
              display: true,
              text: "Chart.js Line Chart"
            }
          }
        }
      }
    };
    this.drawChart("pie-group-services");
    this.drawChart("bar-group-statuses");
    this.drawChart("bar-online-vs-offline");
    this.drawChart("bar-group-months");
    this.drawChart("created-last-month");
  },

  methods: {
    drawChart(chartID: any) {
      const ctx = (document.getElementById(chartID) as any).getContext("2d");
      const myChart = new Chart(ctx, this.chartConfig[chartID]);
    },
    fillLastYear() {
      const data = [];
      for (let index = 0; index < 13; index++) {
        data.push(Math.round(Math.random() * 10) * index * 10);
      }
      return data;
    },
    getDaysBetweenDates(startDate: any, endDate: any) {
      const now = startDate.clone(),
        dates = [];
      while (now.isSameOrBefore(endDate)) {
        dates.push(now.format("DD.MM.YYYY"));
        now.add(1, "days");
      }
      return dates;
    },
    generateLast30DaysData() {
      const data = [];
      for (let index = 1; index <= 30; index++) {
        data.push(Math.round(Math.random() * 10) * index * 100);
      }
      return data;
    }
  }
});
